import React, { useEffect } from "react";
import Brzmi from "./Brzmi/Brzmi";
import MainProducts from "./MainProducts/MainProducts";
import Banner from "./Banner/Banner";
import Footer from "../Footer/Footer";
import "./HomeStyle.scss";

const Home = () => {
  useEffect(() => {
    scrollTo();
  });
  const scrollTo = () => {
    try {
      window.scroll({
        top: 0,
        left: 0,
        behavior: "smooth",
      });
    } catch (error) {
      window.scrollTo(0, 0);
    }
  };
  return (
    <React.Fragment>
      <Banner />
      <MainProducts />
      <Brzmi />
      <Footer />
    </React.Fragment>
  );
};

export default Home;
