import HamburgerHide from "../../../../Assets/menu--open.svg";
import HamburgerClose from "../../../../Assets/menu--closed.svg";
import Logo from "../Logo/Logo";
import React, { useState } from "react";
import { NavLink } from "react-router-dom";

const Hamburger = () => {
  const [shownMenu, changeShowMenu] = useState(false);

  return (
    <React.Fragment>
      <Logo />

      {!shownMenu ? (
        <img
          className="burger-open"
          src={HamburgerHide}
          onClick={() => {
            changeShowMenu((shownMenu) => !shownMenu);
          }}
        />
      ) : null}
      {shownMenu ? (
        <ul className="burger__menu">
          <li className="burger__menu__element">
            <NavLink
              to="/map"
              activeClassName="active"
              onClick={() => {
                changeShowMenu((shownMenu) => !shownMenu);
              }}
              className="burger__menu__element__link"
            >
              Mapa czujników
            </NavLink>
          </li>
          <li className="burger__menu__element">
            <a
              href="http://srodowiskowokolicy.pl/wykresyg.php"
              onClick={() => {
                changeShowMenu((shownMenu) => !shownMenu);
              }}
              className="burger__menu__element__link"
            >
              Wykresy
            </a>
          </li>
          <li className="burger__menu__element">
            <NavLink
              to="/"
              onClick={() => {
                changeShowMenu((shownMenu) => !shownMenu);
              }}
              className="burger__menu__element__link"
            >
              Czujniki
            </NavLink>
          </li>
          <li className="burger__menu__element">
            <NavLink
              to="/oNas"
              onClick={() => {
                changeShowMenu((shownMenu) => !shownMenu);
              }}
              className="burger__menu__element__link"
            >
              O Nas
            </NavLink>
          </li>
          <li className="burger__menu__element">
            <NavLink
              to="/kontakt"
              onClick={() => {
                changeShowMenu((shownMenu) => !shownMenu);
              }}
              className="burger__menu__element__link"
            >
              Kontakt
            </NavLink>
          </li>
        </ul>
      ) : null}
      {shownMenu ? <div className="overlay" /> : null}
      {shownMenu ? (
        <img
          className="burger-close"
          src={HamburgerClose}
          onClick={() => {
            changeShowMenu((shownMenu) => !shownMenu);
          }}
        />
      ) : null}
    </React.Fragment>
  );
};

export default Hamburger;
