import React, { useEffect } from "react";
import Brzmi from "./Brzmi/Brzmi";
import Banner from "./Banner/Banner";
import TextContainer from "./TextContainer/TextContainer";
import Footer from "../Footer/Footer";
import "./KontaktStyle.scss";
const ONas = () => {
  useEffect(() => {
    scrollTo();
  });
  const scrollTo = () => {
    try {
      window.scroll({
        top: 0,
        left: 0,
        behavior: "smooth",
      });
    } catch (error) {
      window.scrollTo(0, 0);
    }
  };
  return (
    <React.Fragment>
      <Banner />
      <TextContainer></TextContainer>
      <Brzmi />
      <Footer />
    </React.Fragment>
  );
};

export default ONas;
