import React, { useState, useEffect } from "react";
import Dexie from "dexie";
import Menu from "./MapPowietrzeComponents/Menu";
import ReactSearchBox from "react-search-box";
import {
  Map,
  Marker,
  Tooltip,
  TileLayer,
  Circle,
  ScaleControl,
  ZoomControl,
} from "react-leaflet";
import L from "leaflet";
import skalaKolorow from "../../Assets/group-3.svg";
import skalaKolorowMobile from "../../Assets/skala_kol_mobile.svg";
import Popup from "./MapPowietrzeComponents/Popup";
import Dropdown from "./MapPowietrzeComponents/Dropdown";
import "./MapPowietrzeStyle.scss";

const MapPowietrze = () => {
  const [lat, latChange] = useState(50.1093);
  const [lng, lngChange] = useState(18.836207);
  const [zoom, zoomChange] = useState(15);
  const [dataToMap, dataToMapChange] = useState([]);
  const [width, widthChange] = useState(0);
  const [data, dataChange] = useState([
    {
      key: "Gostyń, Rybnicka",
      value: "Gostyń, Rybnicka",
      positionDlugosc: 50.1093,
      positionSzerokosc: 18.836207,
      temperatura: "20",
      cisnienie: 20,
      wilgoc: 20,
      pm10: 20,
      pm25: 20,
      pm100: 20,
      procent_pm10: 20,
      procent_pm25: 20,
      kolor: "#92d050",
    },
    {
      key: "Gostyń, OSP",
      value: "Gostyń, OSP",
      positionDlugosc: 18.881484,
      positionSzerokosc: 50.111855,
      temperatura: " 20",
      cisnienie: 20,
      wilgotnosc: 20,
      pm10: 20,
      pm25: 20,
      pm100: 20,
      procent_pm10: 20,
      procent_pm25: 20,
      kolor: "#92d050",
    },
    {
      key: "Bytom, Miechowice",
      value: "Bytom, Miechowice",
      positionDlugosc: 18.859736,
      positionSzerokosc: 50.36103,
      temperatura: "20",
      cisnienie: 20,
      wilgotnosc: 20,
      pm10: 20,
      pm25: 20,
      pm100: 20,
      procent_pm10: 20,
      procent_pm25: 20,
      kolor: "#92d050",
    },
    {
      key: "Orzesze, Woszczyce",
      value: "Orzesze, Woszczyce",
      positionDlugosc: 18.758083,
      positionSzerokosc: 50.091473,
      temperatura: "20",
      cisnienie: 20,
      wilgotnosc: 20,
      pm10: 20,
      pm25: 20,
      pm100: 20,
      procent_pm10: 20,
      procent_pm25: 20,
      kolor: "#92d050",
    },
    {
      key: "Gostyń, Dębowa",
      value: "Gostyń, Dębowa",
      positionDlugosc: 18.857061,
      positionSzerokosc: 50.116219,
      temperatura: 20,
      cisnienie: 20,
      wilgotnosc: 20,
      pm10: 20,
      pm25: 20,
      pm100: 20,
      procent_pm10: 20,
      procent_pm25: 20,
      kolor: "#92d050",
    },
    {
      key: "Gostyń, średnie",
      value: "Gostyń, średnie",
      positionDlugosc: 18.865384,
      positionSzerokosc: 50.111349,
      temperatura: 20,
      cisnienie: 20,
      wilgotnosc: 20,
      pm10: 20,
      pm25: 20,
      pm100: 20,
      procent_pm10: 20,
      procent_pm25: 20,
      kolor: "#92d050",
    },
    {
      key: "Radwanice, Wrocławska",
      value: "Radwanice, Wrocławska",
      positionDlugosc: 17.111003,
      positionSzerokosc: 51.052088,
      temperatura: 20,
      cisnienie: 20,
      wilgotnosc: 20,
      pm10: 20,
      pm25: 20,
      pm100: 20,
      procent_pm10: 20,
      procent_pm25: 20,
      kolor: "#92d050",
    },
    {
      key: "Gostyń, Miarowa",
      value: "Gostyń, Miarowa",
      positionDlugosc: 18.881086,
      positionSzerokosc: 50.09831,
      temperatura: 20,
      cisnienie: 20,
      wilgotnosc: 20,
      pm10: 20,
      pm25: 20,
      pm100: 20,
      procent_pm10: 20,
      procent_pm25: 20,
      kolor: "#92d050",
    },
    {
      key: "Wrocław, Mennicza",
      value: "Wrocław, Mennicza",
      positionDlugosc: 17.034652,
      positionSzerokosc: 51.106105,
      temperatura: 20,
      cisnienie: 20,
      wilgotnosc: 20,
      pm10: 20,
      pm25: 20,
      pm100: 20,
      procent_pm10: 20,
      procent_pm25: 20,
      kolor: "#92d050",
    },
    {
      key: "Wymysłów, ul. Jana Pawła II",
      value: "Wymysłów, ul. Jana Pawła II",
      positionDlugosc: 18.989514,
      positionSzerokosc: 50.407828,
      temperatura: 20,
      cisnienie: 20,
      wilgotnosc: 20,
      pm10: 20,
      pm25: 20,
      pm100: 20,
      procent_pm10: 20,
      procent_pm25: 20,
      kolor: "#92d050",
    },
  ]);
  const [actualCoord, actualCoordChange] = useState([
    {
      positionDlugosc: 6,
      positionSzerokosc: 25,
    },
  ]);
  const [miejsce, miejsceChange] = useState("");
  const [pm10, pm10Change] = useState(null);
  const [pm25, pm25Change] = useState(null);
  const [shownPopup, shownPopupChange] = useState(false);
  const [pm100, pm100Change] = useState(null);
  const [pomiar, pomiarChange] = useState(null);
  const [inputValue, inputValueChange] = useState("Miasto, ulica");
  const [dataPomiaru, dataPomiaruChange] = useState("");
  const [procent_pm25, procent_pm25Change] = useState(null);
  const [procent_pm10, procent_pm10Change] = useState(null);
  const [wilgoc, wilgocChange] = useState(null);
  const [temperatura, temperaturaChange] = useState(null);
  const [cisnienie, cisnienieChange] = useState(null);
  const [wykres, wykresChange] = useState(
    "http://srodowiskowokolicy.pl/9CA72FC4F5FC.jpg"
  );

  useEffect(() => {
    console.log(zoom);
    getPHP();
    window.addEventListener("resize", updateWindowDimensions);
    updateWindowDimensions();
    scrollTo();
    return function cleanup() {
      window.removeEventListener("resize", updateWindowDimensions);
    };
  }, []);

  useEffect(() => {
    getPHP();
  }, [miejsce]);
  const scrollTo = () => {
    try {
      window.scroll({
        top: 0,
        left: 0,
        behavior: "smooth",
      });
    } catch (error) {
      window.scrollTo(0, 0);
    }
  };
  useEffect(() => {
    console.log(zoom);

    if (miejsce) indexedDB();
  }, [procent_pm10]);

  const updateWindowDimensions = () => {
    widthChange(window.innerWidth);
  };
  const showPopup = () => {
    shownPopupChange(true);
  };
  const showPopupWithUpdate = (value) => {
    let copyActualCoord = {};
    copyActualCoord.positionSzerokosc = value.dlugosc;
    copyActualCoord.positionDlugosc = value.szerokosc;
    actualCoordChange(copyActualCoord);
    miejsceChange(value.nazwa_czujnika);
    dataPomiaruChange(value.data);
    temperaturaChange(value.temperatura);
    cisnienieChange(value.cisnienie);
    wilgocChange(value.wilgotnosc);
    pm10Change(value.pm1);
    pm100Change(value.pm10);
    latChange(value.dlugosc);
    lngChange(value.szerokosc);
    pm25Change(value.pm2);
    procent_pm25Change(value.procent_PM25);
    wykresChange(
      "http://srodowiskowokolicy.pl/" + value.nazwa_stacji + ".jpg "
    );
    procent_pm10Change(value.procent_PM10);
    shownPopupChange(true);
  };
  const hidePopup = (hidePopup) => {
    shownPopupChange(hidePopup);
  };
  const getPHP = () => {
    const url = "http://srodowiskowokolicy.pl/wspolne_powietrze_json.php";
    fetch(url)
      .then((response) => response.text())
      .then((contents) => sensorsMap(contents))
      .catch(() => {
        secondFetch();
      });
  };

  const sensorsMap = (contents) => {
    let txt = document.createElement("textarea");
    txt.innerHTML = contents;
    contents = txt.value;

    let contentsToJSON = JSON.parse(contents);
    let data1 = { ...data };

    updateFromDefault(contentsToJSON);

    for (let i = 0; i <= 13; i++) {
      data1[i].key = contentsToJSON["czujniki"][i].nazwa_czujnika;
      data1[i].value = decodeURIComponent(
        contentsToJSON["czujniki"][i].nazwa_czujnika
      );
      data1[i].positionSzerokosc = contentsToJSON["czujniki"][i].dlugosc;
      data1[i].positionDlugosc = contentsToJSON["czujniki"][i].szerokosc;
      data1[i].dataPomiaru = contentsToJSON["czujniki"][i].data;
      data1[i].temperatura = contentsToJSON["czujniki"][i].temperatura;
      data1[i].pm10 = contentsToJSON["czujniki"][i].pm1;
      data1[i].pm25 = contentsToJSON["czujniki"][i].pm2;
      data1[i].pm100 = contentsToJSON["czujniki"][i].pm10;
      data1[i].wilgoc = contentsToJSON["czujniki"][i].wilgotnosc;
      data1[i].cisnienie = contentsToJSON["czujniki"][i].cisnienie;
      data1[i].procent_pm10 = contentsToJSON["czujniki"][i].procent_PM10;
      data1[i].procent_pm25 = contentsToJSON["czujniki"][i].procent_PM25;
      data1[i].kolor = contentsToJSON["czujniki"][i].kolor_caqi;
      data1[i].nazwa_stacji = contentsToJSON["czujniki"][i].nazwa_stacji;
    }

    dataChange(data1);
  };
  function showSensor(value) {
    showPopup();
    console.log(value);
    let copyActualCoord = {};
    copyActualCoord.positionSzerokosc = value.positionSzerokosc;
    copyActualCoord.positionDlugosc = value.positionDlugosc;
    actualCoordChange(copyActualCoord);
    miejsceChange(value.value);
    dataPomiaruChange(value.dataPomiaru);
    temperaturaChange(value.temperatura);
    cisnienieChange(value.cisnienie);
    wilgocChange(value.wilgoc);
    pm10Change(value.pm10);
    pm100Change(value.pm100);
    latChange(value.positionSzerokosc);
    lngChange(value.positionDlugosc);
    pm25Change(value.pm25);
    procent_pm25Change(value.procent_pm25);
    wykresChange(
      "http://srodowiskowokolicy.pl/" + value.nazwa_stacji + ".jpg "
    );

    procent_pm10Change(value.procent_pm10);
    getPHP();
  }

  const secondFetch = () => {
    if (typeof data[3].temperatura !== "number") {
      const url = "https://srodowiskowokolicy.pl/wspolne_powietrze_json.php";
      fetch(url)
        .then((response) => response.text())
        .then((contents) => sensorsMap(contents))
        .catch(() =>
          console.log("Can’t access " + url + " response. Blocked by browser?")
        );
    }
  };
  const indexedDB = () => {
    var db = new Dexie("CzujnikiPowietrze");
    db.version(1).stores({
      powietrze: "++id,miejsce",
    });

    //
    // Manipulate and Query Database
    //
    db.powietrze.clear();
    db.powietrze
      .add({
        miejsce: miejsce,
      })
      .catch(function (e) {
        alert("Error: " + (e.stack || e));
      });
  };
  const updateFromDefault = (contentsToJSON) => {
    var db = new Dexie("CzujnikiPowietrze");
    db.version(1).stores({
      powietrze: "++id,miejsce",
    });

    const getPosts = async () => {
      const allPosts = await db.powietrze.toArray();

      if (allPosts.length > 0) {
        miejsceChange(allPosts[0].miejsce);
        for (let i = 0; i < contentsToJSON["czujniki"].length; i++) {
          if (
            contentsToJSON["czujniki"][i].nazwa_czujnika == allPosts[0].miejsce
          ) {
            let copyActualCoord = {};
            copyActualCoord.positionSzerokosc =
              contentsToJSON["czujniki"][i].dlugosc;
            copyActualCoord.positionDlugosc =
              contentsToJSON["czujniki"][i].szerokosc;
            temperaturaChange(contentsToJSON["czujniki"][i].temperatura);
            miejsceChange(contentsToJSON["czujniki"][i].nazwa_czujnika);
            cisnienieChange(contentsToJSON["czujniki"][i].cisnienie);
            latChange(contentsToJSON["czujniki"][i].dlugosc);
            lngChange(contentsToJSON["czujniki"][i].szerokosc);
            wilgocChange(contentsToJSON["czujniki"][i].wilgotnosc);
            pm10Change(contentsToJSON["czujniki"][i].pm1);
            pm25Change(contentsToJSON["czujniki"][i].pm2);
            pm100Change(contentsToJSON["czujniki"][i].pm10);
            procent_pm10Change(contentsToJSON["czujniki"][i].procent_PM10);
            actualCoordChange(copyActualCoord);
            procent_pm25Change(contentsToJSON["czujniki"][i].procent_PM25);
            wykresChange(
              "http://srodowiskowokolicy.pl/" +
                contentsToJSON["czujniki"][i].nazwa_stacji +
                ".jpg"
            );
            dataPomiaruChange(contentsToJSON["czujniki"][i].data);
          }
        }
      } else {
        let copyActualCoord = {};
        copyActualCoord.positionSzerokosc =
          contentsToJSON["czujniki"][9].dlugosc;
        copyActualCoord.positionDlugosc =
          contentsToJSON["czujniki"][9].szerokosc;
        temperaturaChange(contentsToJSON["czujniki"][9].temperatura);
        miejsceChange(contentsToJSON["czujniki"][9].nazwa_czujnika);
        cisnienieChange(contentsToJSON["czujniki"][9].cisnienie);
        wilgocChange(contentsToJSON["czujniki"][9].wilgotnosc);
        pm10Change(contentsToJSON["czujniki"][9].pm1);
        pm25Change(contentsToJSON["czujniki"][9].pm2);
        pm100Change(contentsToJSON["czujniki"][9].pm10);
        procent_pm10Change(contentsToJSON["czujniki"][9].procent_PM10);
        actualCoordChange(copyActualCoord);
        procent_pm25Change(contentsToJSON["czujniki"][9].procent_PM25);
        wykresChange(
          "http://srodowiskowokolicy.pl/" +
            contentsToJSON["czujniki"][9].nazwa_stacji +
            ".jpg"
        );
        dataPomiaruChange(contentsToJSON["czujniki"][9].data);
      }
    };

    getPosts();
    contentsToJSON["czujniki"].sort(function (a, b) {
      if (a.nazwa_czujnika > b.nazwa_czujnika) {
        return 1;
      }
      if (b.nazwa_czujnika > a.nazwa_czujnika) {
        return -1;
      }
      return 0;
    });
    dataToMapChange(contentsToJSON["czujniki"]);
  };

  const toMap = (value) => {
    showPopup();
    let copyActualCoord = {};
    copyActualCoord.positionSzerokosc = value.positionSzerokosc;
    copyActualCoord.positionDlugosc = value.positionDlugosc;
    lngChange(value.positionSzerokosc);
    latChange(value.positionDlugosc);
    temperaturaChange(value.temperatura);
    miejsceChange(value.value);
    pomiarChange(value.pomiar);
    dataPomiaruChange(value.dataPomiaru);
    cisnienieChange(value.cisnienie);
    wilgocChange(value.wilgoc);
    pm10Change(value.pm10);
    pm25Change(value.pm25);
    pm100Change(value.pm100);
    actualCoordChange(copyActualCoord);
    procent_pm25Change(value.procent_pm25);
    wykresChange(
      "http://srodowiskowokolicy.pl/" + value.nazwa_stacji + ".jpg "
    );
    dataPomiaruChange(value.dataPomiaru);
    procent_pm10Change(value.procent_pm10);
    getPHP();
  };
  const showSensorToChild = (showSensorToChild) => {
    if (showSensorToChild) showSensor(showSensorToChild);
  };
  const viewport = {
    center: [lat, lng],
    zoom: zoom,
  };
  const onViewportChanged = (viewport) => {
    zoomChange(viewport.zoom);
    latChange(viewport.center[0]);
    lngChange(viewport.center[1]);
  };
  return (
    <section className="map">
      <Menu />
      <div className="map__search">
        <ReactSearchBox
          placeholder="Miasto, ulica"
          value={inputValue}
          data={data}
          onSelect={toMap}
          onFocus={() => {
            document.getElementsByClassName("sc-bdVaJa")[0].value = "";
          }}
        />
      </div>
      <Map viewport={viewport} onViewportChanged={onViewportChanged}>
        <ScaleControl />
        <ZoomControl
          onChange={() => {
            console.log("sdasd");
          }}
        />
        {width > 800 ? (
          <img className="map__scale" src={skalaKolorow} />
        ) : (
          <img className="map__scale" src={skalaKolorowMobile} />
        )}
        <TileLayer
          attribution='&amp;copy <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
          url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
        />
        {dataToMap.length >= data.length
          ? dataToMap.map((czujnik) => {
              let icon = czujnik.kolor_caqi.replace(/#/, "kolor");
              const iconMap = new L.Icon({
                iconUrl: require(`./../../Assets/${icon}.svg`),
                iconRetinaUrl: require(`./../../Assets/${icon}.svg`),
                iconAnchor: null,
                popupAnchor: [0, 0],
                shadowUrl: null,
                shadowSize: null,
                shadowAnchor: null,
                iconSize: new L.Point(30, 39),
                className: "leaflet-div-icon",
              });
              return (
                <div key={czujnik.nazwa_stacji}>
                  <div>
                    <Circle
                      center={[czujnik.dlugosc, czujnik.szerokosc]}
                      fillColor={czujnik.kolor_caqi}
                      radius={500}
                      weight={0}
                    />
                    <Marker
                      icon={iconMap}
                      position={[czujnik.dlugosc, czujnik.szerokosc]}
                      onClick={() => showPopupWithUpdate(czujnik)}
                    >
                      <Tooltip>
                        {czujnik.nazwa_czujnika} <br />
                        Temperatura: {czujnik.temperatura} °C <br />
                        Ciśnienie: {czujnik.cisnienie} kPa
                        <br />
                        Wilgotność: {czujnik.wilgotnosc} %<br />
                        PM 1.0 = {czujnik.pm1} μg/m³
                        <br />
                        <span style={{ color: czujnik.kolor_PM25 }}>
                          PM 2.5 ={czujnik.pm2} μg/m³ - {czujnik.procent_PM25} %
                        </span>
                        <br />
                        <span style={{ color: czujnik.kolor_PM10 }}>
                          {" "}
                          PM 10 = {czujnik.pm10} μg/m³ - {czujnik.procent_PM10}{" "}
                          %
                        </span>
                        <br />
                      </Tooltip>
                    </Marker>
                  </div>
                </div>
              );
            })
          : null}
      </Map>
      <Dropdown data={data} showSensor={showSensorToChild} />

      {shownPopup ? (
        <Popup
          miejsce={miejsce}
          procent_pm25={procent_pm25}
          procent_pm10={procent_pm10}
          dataPomiaru={dataPomiaru}
          temperatura={temperatura}
          cisnienie={cisnienie}
          wilgoc={wilgoc}
          pm10={pm10}
          pm25={pm25}
          pm100={pm100}
          wykres={wykres}
          hidePopup={hidePopup}
        />
      ) : null}
    </section>
  );
};
export default MapPowietrze;
