import React from "react";
import { NavLink } from "react-router-dom";
const Menu = () => {
  return (
    <ul className="map__menu">
      <li className="map__menu__element">
        <NavLink className="map__menu__element__link" to="/map">
          Hałas
        </NavLink>
      </li>
      <li className="map__menu__element  active">
        <NavLink
          className="map__menu__element__link"
          to="/mapPowietrze"
          activeClassName="active"
        >
          Powietrze
        </NavLink>
      </li>
    </ul>
  );
};

export default Menu;
