import logo from "../../../../Assets/group-12-copy-5.svg";
import { NavLink } from "react-router-dom";
import React from "react";

const Logo = () => {
  const scrollTo = () => {
    try {
      window.scroll({
        top: 0,
        left: 0,
        behavior: "smooth",
      });
    } catch (error) {
      window.scrollTo(0, 0);
    }
  };
  return (
    <React.Fragment>
      <NavLink to="/" onClick={() => scrollTo()} className="logo__link">
        <img className="logo__link__img" src={logo} />
      </NavLink>
    </React.Fragment>
  );
};

export default Logo;
