import React, { useState, useEffect } from "react";
import Logo from "./HeaderComponents/Logo/Logo";
import Navi from "./HeaderComponents/Navi/Navi";
import Hamburger from "./HeaderComponents/Hamburger/Hamburger";
import "./HeaderStyle.scss";

const Header = () => {
  const [isMobile, changeIsMobile] = useState(false);

  useEffect(() => {
    window.addEventListener("resize", listenForResize);
    listenForResize();
    return function cleanup() {
      window.removeEventListener("resize", listenForResize);
    };
  });

  const listenForResize = () => {
    if (window.innerWidth < 1000) {
      changeIsMobile(true);
    } else {
      changeIsMobile(false);
    }
  };

  return (
    <section className="header">
      {isMobile ? (
        <Hamburger />
      ) : (
        <React.Fragment>
          <Logo />
          <Navi />
        </React.Fragment>
      )}
    </section>
  );
};

export default Header;
