import React, { useEffect, useState } from "react";
import x from "../../../Assets/times.svg";
import PropTypes from "prop-types";

const Popup = ({
  miejsce,
  dataPomiaru,
  pm10,
  pm25,
  pm100,
  wykres,
  procent_pm25,
  procent_pm10,
  temperatura,
  cisnienie,
  wilgoc,
  hidePopup,
}) => {
  const [popupVisibility, hidePopupChild] = useState(true);
  useEffect(() => {
    hidePopup(popupVisibility);
  }, [popupVisibility]);
  return (
    <div className="popup" id="popupDane">
      <div className="popup-powietrze__dane">
        <div className="popup__dane__place">{miejsce}</div>
        <div className="popup__dane__measure">
          <div className="popup__dane__measure__key">Data odczytu</div>
          <div className="popup__dane__measure__value">{dataPomiaru}</div>
        </div>
        <div className="popup__dane-flex">
          <div className="popup__dane-half">
            <div className="popup__dane__measure-description">
              PYŁY ZAWIESZONE
            </div>
            <div className="popup__dane__measure__key">
              Wartości po uśrednienu:
            </div>
            <div className="popup__dane__measure__value">
              PM 1.0 = {pm10} μg/m³
            </div>
            <div className="popup__dane__measure__value">
              PM 2.5 = {pm25} μg/m³ - {procent_pm25} %
            </div>
            <div className="popup__dane__measure__value">
              PM 10 = {pm100} μg/m³ - {procent_pm10} %
            </div>
          </div>
          <div className="popup__dane-half">
            <div className="popup__dane__measure-description">
              DANE POGODOWE
            </div>
            <div className="popup__dane__measure__key">Wartości zmierzone:</div>
            <div className="popup__dane__measure__value">
              Temperatura = {temperatura} °C
            </div>
            <div className="popup__dane__measure__value">
              Ciśnienie = {cisnienie} hPa
            </div>
            <div className="popup__dane__measure__value">
              Wilgotność = {wilgoc} %
            </div>
          </div>
        </div>
      </div>
      <a
        className="popup__wykres"
        onClick={() => window.open(wykres, "_blank")}
      >
        sprawdź wykres
      </a>
      <img
        className="popup__close"
        src={x}
        onClick={() => hidePopupChild(false)}
      />
    </div>
  );
};
Popup.propTypes = {
  miejsce: PropTypes.string,
  dataPomiaru: PropTypes.string,
  pm10: PropTypes.string,
  pm25: PropTypes.string,
  pm100: PropTypes.string,
  wykres: PropTypes.string,
  procent_pm25: PropTypes.string,
  procent_pm10: PropTypes.string,
  temperatura: PropTypes.string,
  cisnienie: PropTypes.string,
  wilgoc: PropTypes.string,
  hidePopup: PropTypes.func,
};

export default Popup;
